<template>
  <div>
    <div class='bar'>
      <div>
        <span class='logo' @click='schoolNav("jnu")'>
          <el-image :src="require('@/../public/assets/jnu.png')" style='width:40px'></el-image>
        </span>
      </div>

      <div>
        <span>
          <el-dropdown @command='schoolNav' class='pageDropdown' :show-timeout='1' size='medium'>
            <span class='dropdownSelector'>
              {{$t('Header.SchoolNav')}}
              <i class="el-icon-arrow-down"></i>
            </span>
            <el-dropdown-menu slot='dropdown'>
              <el-dropdown-item command='network'>
                <el-image :src="require('@/../public/assets/network.png')" class='dropdownImage'></el-image>
                <span class='dropdownSpan'>{{$t('Header.Network')}}</span>
              </el-dropdown-item>
              <el-dropdown-item command='class'>
                <el-image :src="require('@/../public/assets/class.png')" class='dropdownImage'></el-image>
                <span class='dropdownSpan'>{{$t('Header.Class')}}</span>
              </el-dropdown-item>
              <el-dropdown-item command='schedule'>
                <el-image :src="require('@/../public/assets/schedule.png')" class='dropdownImage'></el-image>
                <span class='dropdownSpan'>{{$t('Header.Schedule')}}</span>
              </el-dropdown-item>
              <el-dropdown-item command='exam'>
                <el-image :src="require('@/../public/assets/exam.png')" class='dropdownImage'></el-image>
                <span class='dropdownSpan'>{{$t('Header.Exam')}}</span>
              </el-dropdown-item>
            </el-dropdown-menu>
          </el-dropdown>
        </span>

        <span class='user hidden-xs-only' @click='changePage("userMe")'>
          <el-image :src="require('@/../public/assets/user.png')" class='headerImage'></el-image>
          <span>{{$t('Header.UserInfo')}}</span>
        </span>

        <span class='alert hidden-xs-only' @click='changePage("alertList")'>
          <el-image :src="alertIcon" class='headerImage'></el-image>
          <span>{{$t('Header.Alert')}} ({{alertAmount}})</span>
        </span>

        <span class='hidden-sm-and-up'>
          <el-dropdown @command='userDropdown' class='userDropdown' :show-timeout='1' size='medium'>
            <span class='dropdownSelector'>
              {{$t('Header.UserInfo')}}
              <i class="el-icon-arrow-down"></i>
            </span>
            <el-dropdown-menu slot='dropdown'>
              <el-dropdown-item command='userMe'>
                <el-image :src="require('@/../public/assets/user.png')" class='dropdownImage'></el-image>
                <span class='dropdownSpan'>{{$t('Header.UserInfo')}}</span>
              </el-dropdown-item>
              <el-dropdown-item command='alertList'>
                <el-image :src="alertIcon" class='dropdownImage'></el-image>
                <span class='dropdownSpan'>{{$t('Header.Alert')}}</span>
              </el-dropdown-item>
              <el-dropdown-item command='userHistory'>
                <el-image :src="require('@/../public/assets/history.png')" class='dropdownImage'></el-image>
                <span class='dropdownSpan'>{{$t('User.History')}}</span>
              </el-dropdown-item>
            </el-dropdown-menu>
          </el-dropdown>
        </span>
      </div>

      <div>
        <el-dropdown @command='changeLang' class='languageDropdown'>
          <span class='languageSelector'>
            <el-image :src="require('@/../public/assets/language.png')" style='width:40px'></el-image>
          </span>
          <el-dropdown-menu slot='dropdown'>
            <el-dropdown-item command='cn_zh'>简体中文</el-dropdown-item>
            <el-dropdown-item command='cn_tw'>繁體中文</el-dropdown-item>
            <el-dropdown-item command='en_us'>English</el-dropdown-item>
          </el-dropdown-menu>
        </el-dropdown>

        <el-image :src="require('@/../public/assets/admin.png')" style='width:40px;cursor:pointer' v-if='admin'
          @click='adminPage'></el-image>
      </div>
    </div>
  </div>
</template>

<script>
import axios from 'axios'
import Cookies from 'js-cookie'
import * as server from '../../server.js'

export default {
  name: 'frame-header',
  data () {
    return {
      searchText: '',
      alertAmount: 0,
      alertIcon: require('@/../public/assets/alert.png'),
      admin: false,
    }
  },
  methods: {
    schoolNav (page) {
      switch (page) {
        case 'jnu':
          window.open('https://jnu.edu.cn')
          break;
        case 'network':
          window.open('https://mynet.jnu.edu.cn/app/#modules/customer/myHome.html')
          break;
        case 'class':
          window.open('https://jwxk.jnu.edu.cn/')
          break;
        case 'schedule':
          window.open('https://jw.jnu.edu.cn/new/index.html')
          break;
        case 'exam':
          window.open('https://jw.jnu.edu.cn/new/index.html')
          break;
        default:
          break;
      }
    },
    changeLang (lang) {
      this.switchLang(lang);
    },
    changePage (target) {
      this.$router.push({
        name: target
      })
    },
    getAlertAmount () {
      if (!Cookies.get('loginCookie')) {
        this.goLogin();
        return;
      }

      const loadingInstance = this.loading();

      axios({
        method: 'post',
        url: server.default.getAlertAmount,
        data: new URLSearchParams({
          'cookie': Cookies.get('loginAs')
        })
      }).then((response) => {
        loadingInstance.close();
        if (response.status != 200) {
          this.alert(this.$t('System.NetworkIssue'), this.$t('System.Error'))
        }
        else {
          if (response.data.status == 'ALERT_GETAMOUNT_COMPLETE') {
            this.alertAmount = response.data.amount;
            if (this.alertAmount > 0) {
              this.alertIcon = require('@/../public/assets/alert_red.png')
            }
            else {
              this.alertIcon = require('@/../public/assets/alert.png')
            }
          }
        }
      });
    },
    getUser () {
      if (!Cookies.get('loginCookie')) {
        this.goLogin();
        return;
      }

      const loadingInstance = this.loading();

      axios({
        method: 'post',
        url: server.default.user,
        data: new URLSearchParams({
          'type': 'me',
          'cookie': Cookies.get('loginAs')
        })
      }).then((response) => {
        loadingInstance.close();
        if (response.status != 200) {
          this.alert(this.$t('System.NetworkIssue'), this.$t('System.Error'))
        }
        else {
          if (response.data == 'USER_NOMATCH') {
            this.alert(this.$t('System.IllegalRequest'), this.$t('System.Error'));
          }
          else if (response.data.status == 'USER_FOUND') {
            if (response.data.userstatus >= 10) {
              sessionStorage.setItem('ia', true)
              this.admin = true;
            }
            else {
              sessionStorage.setItem('ia', false)
              this.admin = false;
            }
          }
          else {
            this.alert('Unexpected Response!', this.$t('System.Error'));
          }
        }
      });
    },
    adminPage () {
      if (this.admin) {
        this.$router.push({
          name: 'admin'
        })
      }
    },
    readAlert (amount) {
      if (this.alertAmount >= amount) {
        this.alertAmount = this.alertAmount - amount;
      }
      if (this.alertAmount > 0) {
        this.alertIcon = require('@/../public/assets/alert_red.png')
      }
      else {
        this.alertIcon = require('@/../public/assets/alert.png')
      }
    },
    userDropdown (target) {
      this.$router.push({
        name: target
      })
    },
  },
  mounted () {
    this.$bus.$on('readAlert', this.readAlert);
    this.getUser();
    this.getAlertAmount();
  },
  beforeDestroy () {
    this.$bus.$off('readAlert', this.readAlert);
  }
}
</script>

<style scoped>
.bar {
  background-color: rgb(255, 255, 255);
  height: 70px;
  width: 100%;
  display: flex;
  justify-content: space-between;
}
.logo {
  float: left;
  margin-left: 10px;
  margin-top: 15px;
  cursor: pointer;
}
.search .el-input {
  margin-top: 15px;
  font-size: 20px;
}
.searchButton {
  cursor: pointer;
}
.el-dropdown-menu__item span {
  font-size: 20px;
  margin-left: 15px;
}
.pageDropdown {
  font-size: 20px;
  margin-top: 15px;
  cursor: pointer;
}
.userDropdown {
  font-size: 20px;
  margin-top: 15px;
  cursor: pointer;
  margin-left: 20px;
}
.dropdownSpan {
  position: relative;
  top: -10px;
}
.dropdownImage {
  width: 40px;
}
.user,
.alert {
  font-size: 20px;
  margin-top: 15px;
  margin-left: 20px;
  cursor: pointer;
}
.headerImage {
  width: 25px;
  position: relative;
  top: 8px;
  margin-right: 5px;
}
.languageDropdown {
  font-size: 20px;
  margin-top: 15px;
  cursor: pointer;
}
</style>